.title {
  justify-content: center;
  display: flex;
  color: blue;
  font-style: italic;
}

.heading {
  text-align: center;
}

.caclContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0;
}

.theCalculator {
  height: 295px;
  width: 350px;
  background-color: rgb(214, 214, 214);
}

.editable {
  width: 100%;
  height: 15%;
  font-size: 200%;
  outline: none;
  text-align: right;
}

.row1 {
  display: flex;
}

.AC,
.plusNminus,
.percentage,
.division {
  flex: 1;
  height: 50px;
}

.row2 {
  display: flex;
}

.seven,
.eight,
.nine,
.multiply {
  flex: 1;
  height: 50px;
}

.row3 {
  display: flex;
}

.four,
.five,
.six,
.minus {
  flex: 1;
  height: 50px;
}

.row4 {
  display: flex;
}

.one,
.two,
.three,
.plus {
  flex: 1;
  height: 50px;
}

.row5 {
  display: flex;
}

.zero {
  flex: 1;
}

.dot,
.equals {
  flex: 0.5;
  height: 50px;
}

.arithmetic {
  background-color: orange;
}

button {
  border: 1px solid gray;
}

button:hover {
  background-color: aqua;
}
